

import { Inter, Montserrat } from 'next/font/google';
import './globals.css';
import { cn } from '@/lib/utils';
import { Toaster } from '@/components/ui/toaster';
import { AppStateProvider } from '@/components/providers/state-provider';
import { User } from '@/lib/types';
import { cookies } from 'next/headers';
import { findUserById } from '@/lib/data';
import Header from '@/components/header';

const inter = Inter({ subsets: ['latin'], variable: '--font-inter' });
const montserrat = Montserrat({
  subsets: ['latin'],
  variable: '--font-montserrat',
  weight: ['400', '700'],
});

export const metadata = {
  title: 'Echo - Share and Save on Group Orders',
  description: 'Echo is a platform to connect with people in your building to share orders and save money.',
};

async function getUser(): Promise<User | null> {
    const cookieStore = cookies();
    const userId = cookieStore.get('session_userId')?.value;
    
    if (userId) {
        try {
            const user = await findUserById(userId);
            return user;
        } catch (error) {
            console.error("Failed to fetch user:", error);
            return null;
        }
    }
    return null;
}

export default async function RootLayout({ children }: { children: React.ReactNode }) {
  const user = await getUser();

  return (
    <html lang="en" suppressHydrationWarning>
      <body className={cn(
        'min-h-screen bg-background font-sans antialiased',
        inter.variable,
        montserrat.variable
      )}>
        <AppStateProvider user={user}>
          <div className="relative flex min-h-dvh flex-col bg-background">
              <Header />
              <main className="flex-1 py-8">{children}</main>
          </div>
          <Toaster />
        </AppStateProvider>
      </body>
    </html>
  );
}
